.array-container{
    position: absolute;
    left: 200px;
    margin-top: 80px;
    min-height: 600px;
}

.array-bar{
    background-color: coral;
    display: inline-block;
    margin: 0 1px;
}
.nav-bar{
    background-color: burlywood;
    margin-left: 20%;
}
.generate-array-btn{
    float: left;
    margin: 10px;
    height: 30px;
}
.slide-container{
    float: left;
    margin: 10px;
    height: 40px;
}
.range-value{
    margin: 15px 0px;
    float: left;
    opacity: 0.7;
}
.slider::before {
    content: "10";
    color: black;
    font-size: 0.8rem;
    position: absolute;
    left: 45%;
    top: 15%;
    /* float: right; */
    /* top: 26% */
}
.slider::after{
    content: "180";
    color: black;
    font-size: 0.8rem;
    position: absolute;
    left: 55%;
    top:15%;
    /* float: right; */
    /* top: 26% */
}
.slider{
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    background: linear-gradient(90deg, rgb(117,252,117) 60%, rgb(214,214,214) 60%);
    background-color: red;
    outline: none;
    opacity: 0.7;
    -webkit-transition: 0.2%;
    transition: 0.2%;
    border-radius: 12px;

}
.slider:hover{
    opacity: 1;
}
.slider::-webkit-slider-thumb{
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    background: coral;
    border-radius: 50%;
    cursor: pointer;
}
.slider::-moz-range-thumb{
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    background: coral;
    border-radius: 50%;
    cursor: pointer;
}
.sorting-algos-btn button{
    float: left;
    margin: 10px;
    height: 30px;
}
.sort{
    float: left;
    margin: 10px; 
    height: 30px;
}